.wrapper {
  margin-top: 60px;
  text-align: left;
  white-space: nowrap;
}
.wrapper .container {
  position: relative;
  height: 10px;
  background-color: #F2F2F2;
  border-radius: 6px;
}
.wrapper .container .progress {
  display: inline-block;
  vertical-align: top;
}
.wrapper .container .progress {
  height: 100%;
  background-color: #FFCC33;
  border-radius: 6px;
}
.wrapper .container .tip {
  position: absolute;
  top: 0;
  text-align: center;
  transform: translate(-50%, -100%);
}
.wrapper .container .tip > i {
  display: inline-block;
  width: 0;
  height: 0;
  border: 8px solid #FFCC33;
  border-bottom: none;
  border-right-color: transparent;
  border-left-color: transparent;
}
.wrapper .step {
  position: relative;
  min-height: 50px;
}
.wrapper .step > div {
  position: absolute;
  top: 0;
  text-align: center;
}
