.grade_wrapper {
  position: relative;
  padding: 20px 15px;
  text-align: center;
}
.grade_wrapper_question {
  position: absolute;
  top: 10px;
  right: 15px;
}
.grade_wrapper_avator {
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.grade_wrapper_avator > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.grade_wrapper_tip {
  padding: 2px;
  border-radius: 4px;
  background-color: #FF0000;
  color: #FFF;
}
.card_body {
  display: flex;
  flex-wrap: wrap;
}
.card_body > div {
  flex: 0 0 33.3%;
  text-align: center;
  margin-bottom: 12px;
}
.card_body > div > img {
  width: 50px;
  height: 50px;
}
